import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { IconButton as Button } from '@ali/we-toolbar';
import { ModelbuttonNormal } from '@ali/we-icon';
import { Locale } from '../type';
export interface ToolbarButtonProps {
  controller: Controller;
  locale: Locale | Record<string, any>;
}

const iconStyle: React.CSSProperties = {
  width: 20,
  height: 20,
};

const ToolbarButton: React.FC<ToolbarButtonProps> = (props) => {
  const { controller, locale = {} } = props;

  const handleClick = React.useCallback(
    () => controller.dispatch('insertTemplateButton'),
    [controller],
  );

  return (
    <Button
      title={locale.title}
      testid="bi-toolbar-templeButton"
      icon={<ModelbuttonNormal style={iconStyle} />}
      onClick={handleClick}
    />
  );
};

export default React.memo(ToolbarButton);
